<template>

<div class="info_page">
	<!-- <header-top></header-top> -->


<!-- 	<div class="info_content" id='wrapper_content' ref='wrapper_content'>
	
	<ul>
		<li @click='refresh'>refresh()</li>
		<li v-for='item in 10' :class="{'hot':item==50}">{{item +"==="+ new Date().toLocaleString()}}</li>
		<li>
			<router-link to='info/infoDetail'>href</router-link>
                <router-link  :to="{path: '/shop', query: {geohash, id: orderDetail.restaurant_id}}">再来一单</router-link>
		</li>
	</ul>

</div> -->


	<header-top title='资讯行情'></header-top>
	<div class="fixed-top-div"></div>

	    <div class="content index" ref='wrapper_content'>
	        <div class="list" ref='wrapper_content2'>
	            <ul>
					<li v-for="item in newsList " @click='toPage("/info/infoDetail?nId="+item.n_id)'>
	                    <div class="info">
	                        <p>{{ item.n_resume }}</p>
	                        <p><span>{{ item.n_date.substring(0,10) }}</span></p>
	                    </div>
	                    <div class="img">
	                        <img v-bind:src=" item.n_image_url.split(',')[0] " alt=""/>
	                    </div>
	                </li>       
                </ul>
                
                <div v-if='hasMore' class='more'>
					<img src="../../assets/imgs/load.gif" alt="">
				</div>	
				<div v-else class='more' style='padding:.5rem 0'>
					<span>没有了...</span>
				</div>
	        </div>
	    </div>
	




	<transition name='route-slide'>
		<router-view :newsList="newsList"></router-view>
	</transition>
	
	

</div>

</template>
<script>


import BScroll from 'better-scroll'

import headerTop from '@/components/header/header'

import {loadMore} from '@/config/util'

	export default{
		data(){
			return{
				count:100,
				scroll:{},
				pageNo:1,
				newsList:[],
				preventRepeat:false,
				hasMore:true
			}
		},
		components:{
			headerTop
		},
		mounted (){


			this.$nextTick(()=>{
				this.queryNews();

				loadMore(this.$refs.wrapper_content2,this.load);

				// this.scrollList();
			})
			
		},
		methods:{


			load(){

				if(this.preventRepeat){
					return
				}
				this.pageNo ++;
				this.preventRepeat = true;

				console.log("page+"+this.pageNo);

				this.$http.post('/newsList',{pageNo:this.pageNo}).then((res)=>{
					if(res.data.code==0){

						console.log(res.data.response);
						this.newsList = this.newsList.concat(res.data.response);


						if(res.data.response.length<10){
							this.hasMore = false;
						}else{
							// setTimeout(()=>{
								this.preventRepeat = false;
							// },3000)
						}
					}
				})

			},
			//
			queryNews(){
				this.$http.post('/newsList',{pageNo:this.pageNo}).then((res)=>{
					if(res.data.code==0){
						this.newsList = res.data.response;
					}
				})
			},
			toPage(url){
				this.$router.push(url);
			},
			scrollList (){	
				// console.log(this.$refs.wrapper_content);
				this.scroll = new BScroll(this.$refs.wrapper_content,{
					click:true
				})
				// setTimeout(()=>{
				// 	this.scroll.refresh();
				// },2000)
			},
			refresh(){
				//this.scroll.refresh();
			}


			//

		},
		watch:{
			newsList(){
				console.log(1);
				
			}
		}
		
	}




</script>

<style scoped lang='less'>

@import '../../assets/css/variable.less';


.route-slide-enter-active,.route-slide-leave-active{
	transition: all .3s;
}

.route-slide-enter, .route-slide-leave-active {
    /*transform: translate3d(2rem, 0, 0);*/
    transform: translateX(100%);
    opacity: 0;
}
/*.route-slide-leave-active{
	transform: rotateX(100%);
	opacity:0;
}*/


.more{
	height:3rem;
	img{
		height:auto;
		width:auto;
	}
}

.info_page{
/*	position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 202;*/
    background-color: #fff;

}

.index{
	/*position: fixed;*/
	top:2rem;
	bottom:2.1rem;
	left:0;
	right:0;
}

.list{
	/*.fixed();*/
}


.index{

  li{
    height:80/@root-font-size;
    border-bottom:1px solid #D5D5D5;
    overflow: hidden;
    font-size:.7rem;

    display: flex;
    justify-content: flex-start;
    padding:10/@root-font-size 12/@root-font-size;

    div.info{
      width:60%;
      margin-right:6%;

      /*display: flex;*/
      /*flex-direction: column-reverse;*/
      /*justify-content: space-between;*/
		
	display: flex;

	flex-direction: column;

	align-content: flex-end; 




    }

    p{
      /*height:3em;*/
	
	overflow: hidden;




      /*display: flex;*/
      /*align-items: flex-end;*/
    }
    p:first-child{

/*    	text-indent: 1em;
    	font-size:.6rem;
    	height:70%;
    	line-height: .9rem;
    	overflow: hidden;*/

    	/*text-indent: 1em;*/
    	height:2.6rem;
    	line-height: .9rem;








    }
	p:last-child{
		margin-top:.4rem;
		font-size:.6rem;
	}


    span{
      color:#969696;
      display: block;
      /*margin-top:.2rem;*/
    }
    div.img{
      overflow: hidden;
      width:35%;
      img{
        height:83/@root-font-size;
        width:100%;
      }

    }
  }
  li:last-child{
    border:none;
  }

}
























/* .info_content{

	padding-bottom:40px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 45px;

}

li{
	text-align:center;
	line-height:30px;

	overflow:hidden;

	white-space: nowrap;
	text-overflow: ellipsis;


}


.hot{
	background: #f00;
}
li:nth-child(odd){
	background: #89CDFC;
	color:#fff;

} */



</style>


